@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap');


.branches-top {
  position: relative;
  height: calc( 572px + 30 + 80px + 5px);
  width: 100%;
  background-color: var(--white);
  padding-bottom: 10px;
}

.diver-branches-top {
  padding: 20px 35% 9px 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.branches-top-divider {
  color: var(--yellow);
  background-color: var(--buttonsBlue);
  height: 5px;
  border-radius: 50px;
  width: 300px;
}

.diver-branches-top-text {
  width: 600px;
  font-family: "DM Sans";
  color: var(--buttonsBlue);
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  margin: 30px 0px 0px 0px;
  border: 2.9px solid var(--buttonsBlue);
  border-radius: 15px;
  letter-spacing: 1.1px;
}

.branches-top-card-text-main{
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
  max-width: 100%;
  margin-top: 40px;
}

.branches-top-card {
  box-sizing: border-box;
  border: 2.9px solid var(--buttonsBlue);
  border-radius: 24px;
  /* width: calc(100vw - 950px); */
  width: calc(300px);
  height: 572px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 190%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.2s all ease-in-out;
  position: relative;
}

.branches-top-card:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  /* box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px; */
}


.branches-top-card.bhojuvir {
  background-image: url(../img/top1.png);
}

.branches-top-card.lohta {
  background-image: url(../img/top2.png);
}

.branches-top-card.chitaepur {
  background-image: url(../img/doing.png);
}

.branches-top-card-text {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.branches-top-card-text span:nth-last-child(2) {
  font-family: "Poppins";
  color: var(--black);
  font-size: 48px;
  letter-spacing: 5.7px;
  font-weight: 900;
}

.branches-top-card-text span:nth-last-child(1) {
  font-family: "DM Sans";
  color: var(--black);
  letter-spacing: 5.7px;
}

.branches-top-btn-card {
  text-align: center;
  margin: 40px 50px;
}

.branches-top-btn {
  background-color: var(--red) !important;
  padding: 0.8em 5em !important;
  font-family: 'DM Sans' !important;
  color: var(--white) !important;
  font-weight: 700 !important;
}

