.bottom {
  margin: 0px;
  padding: 25px;
  background-color: var(--black);
  color: var(--white);
  text-align: center;
}

.bottom-divider {
  height: 5px;
  background-color: var(--yellow);
  border-radius: 50px;
  margin: 0px 85px;
}

.bottom-divd{
  margin: 0px 35%;
}

.bottom-phone-email {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom-last-text {
  font-family: Ubuntu;
  color: var(--white);
  text-align: center;
  font-weight: 500;
}

.bottom-4-col {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
  max-width: 100%;
  padding-top: 10px;
  position: relative;
}

.bottom-c.c1{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

.bottom-c {
  margin: 20px;
}

.bottom-c a:hover{
  color: var(--yellow);
}

.bottom-c a{
  color: var(--white);
  text-decoration: none;
  display: flex;
  justify-content: center;
  padding: 2.5px 0px;
}


.bottom-c.c4 a{
  font-size: 17px;
}

.contacts-bottom span:hover {
  color: var(--yellow);
}