.header-top {
  position: sticky;
  top: 0px;
  z-index: 999;
  background-color: transparent;
}

.header {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: transparent;
  width: 100%;
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  z-index: 999;
  position: sticky;
  top: 0px;
  transition: 100ms all ease-in;
}


.header-top-a {
  display: flex;
  justify-content: space-between;
  padding: 9px 60px;
  height: 0;
  background-color: var(--buttonsBlue);
  color: var(--white);
  font-family: 'DM Sans';
  height: 30px;
  transition: 200ms all ease-in-out;
}


.header-logo {
  padding: 20px 1px 1px 30px;
  transition: 0.2s all ease-in-out;
}

.header-logo div {
  background-color: #020100;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.2s all ease-in-out;
}

.header-logo div::before {
  content: "Your Logo";
  color: #fdfffc;
  display: block;
  position: relative;
  top: 45%;
  /* font-weight: bold; */
  text-align: center;
  transition: 10ms all ease-in;
}

.header-nav {
  width: 100%;
}

.header-nav .header-nav-item {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.header-nav-item ul li:hover:after {
	transform: scalex(1);
}

.header-nav-item ul li:after {
	transition: 0.25s ease;
	content: "";
	display: block;
	width: 80px;
	height: 2px;
	background-color: currentcolor;
	transform: scalex(0);
	position: relative;
	top: 4px;
	left: 0.9px;
}

.header-nav .header-nav-item ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-align: center;
}

.header-nav .header-nav-item ul li {
  text-align: center;
  padding: 0px 10px 0px 10px;
}

.header-nav .header-nav-item ul li a {
  text-decoration: none;
  color: #020100;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-left: 2px;
  font-size: 1.1em;
  transition: 200ms ease-in-out all;
}

.header-nav .header-nav-item ul li:nth-child(2)  {
  width: 150px;
}

/* .header-nav .header-nav-item ul li a:hover{
    color: var(--buttonsBlue);
} */

.header-action {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  padding: 10px 30px;
}

.header-action .header-action-item {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
}

.header-action-item-but1 {
    background-color: #235789 !important;
    color: var(--white) !important;
    padding: 6px 21px 6px 25px !important;
    border-radius: 1.1px !important;
}

.header-action-item-but2 {
    color: var(--buttonsBlue) !important;
    padding: 6px 20px 6px 20px !important;
    border-radius: 0px !important;
    border-color: var(--buttonsBlue) !important;
    border-width: 1.8px !important;
}