.about-top {
  width: 100%;
  background-color: var(--buttonsBlue);
  /* margin-bottom: 10px; */
  padding-bottom: 70px;
}

.diver-about-top {
  padding: 20px 35% 9px 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about-top-divider {
  color: var(--yellow);
  background-color: var(--yellow);
  height: 5px;
  border-radius: 50px;
  width: 300px;
}

.diver-about-top-text {
  width: 450px;
  font-family: "DM Sans";
  color: var(--yellow);
  font-size: 2.5rem;
  font-weight: 900;
  padding: 12px 0px;
  text-align: center;
  margin: 30px 0px 0px 0px;
  border: 2.9px solid var(--yellow);
  border-radius: 15px;
  letter-spacing: 1.1px;
}

.about-top-1-text {
  color: var(--white);
  width: 600px;
  position: relative;
  margin-left: 10%;
}

.about-top-photo {
  width: 300px;
  height: 300px;
  /* background-image: url(../img/vipin.jpeg); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 90px;
  border: 2px solid red;
  border-radius: 20px;
}

.about-top-1 {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
  max-width: 100%;
  margin-top: 40px;
  /* margin-left: 60px; */
}



.about-top-1 .member {
  position: relative;
  background-image: var(--black);
  min-height: 500px;
  width: 350px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.member:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

}

.about-top-1 .member .imgBx {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.about-top-1 .member .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-top-1 .member .details {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--yellow);
    color: var(--buttonsBlue);
    transition: 0.2s;
    opacity: 0;
    transition: 200ms all ease-in-out;
}

.about-top-1 .member:hover .details {
    opacity: 1;

}

.about-top-1 .member .details  h2 {
    color: var(--buttonsBlue);
    text-transform: uppercase;
    font-size: 27px;
    letter-spacing: 1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-top-1 .member .details h2 span {
    display: block;
    font-size: 12px;
    font-weight: 300;
}
 
