@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");

:root {
    --topheight : 90px;
}

.branch-body {
    height: calc(100vh - 90px);
}
 
.branch-body::before {
    position: absolute;
    display: block;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    /* height: calc(100vh - var(--topheight)); */
    height: 100%;
    background-position: center;
    background-image: url(../img/bg2.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    transform: scaleX(-1);
    /* mix-blend-mode: difference; */
}

/* .branch-body  {
    background-image: url(../img/bg.jpg);
} */


.branch-body-top {
    display: flex;
    flex-direction: row;
}

.branch-body-top-left {
    padding: 20px;
    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.branch-body-top-left-title {
    font-family: 'Poppins', sans-serif;
    font-size: 64px;
    font-weight: 900;
    color: var(--buttonsBlue);
    mix-blend-mode: difference;
}

.branch-body-top-left-text {
    font-family: 'Poppins';
    font-size: 36px;
    color: var(--white);
    font-weight: 600;
    mix-blend-mode: screen;
}

.branch-body-top-left-btn1 {
    color: var(--white) !important;
    padding: 9px 35px 9px 35px !important;
    border-color: var(--white) !important;
    border-radius: 0px !important;
    mix-blend-mode: luminosity;
}

.branch-body-top-left-btn2 {
    background-color: var(--buttonsBlue) !important;
    color: var(--white) !important;
    padding: 9px 35px 9px 35px !important;
    border-radius: 0px !important;
}

.branch-body-top-right {
    position: relative;
    padding: 20px;
    margin: 40px 40px 0px 0px;
    width: 500px;
    transform-style: preserve-3d;
    /* overflow: hidden; */
}

.branch-body-right-image {
    font-family: "DM Sans", sans-serif;
    font-size: 60px;
    color: var(--white);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: absolute;
    top: 30px;
    left: 0;
    width: 100%;
    height: 90%;
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transition: 300ms ease-in-out all;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transform: perspective(500px) rotateY(0deg);
    border: 2px solid var(--yellow);
}

