.imagegBranch {
  margin: calc(var(--topheight) - 30px);
}

.img-branches-top {
  padding: 20px 35% 9px 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 30px;
}

.img-branches-divider {
  color: var(--buttonsBlue);
  background-color: var(--buttonsBlue);
  height: 5px;
  border-radius: 50px;
  width: 300px;
}

.img-branches-top-text {
  width: 450px;
  font-family: "DM Sans";
  color: var(--buttonsBlue);
  font-size: 2.5rem;
  font-weight: 900;
  padding: 12px 0px;
  text-align: center;
  margin: 30px 0px 0px 0px;
  border: 2.9px solid var(--buttonsBlue);
  border-radius: 15px;
  letter-spacing: 1.1px;
}
